import React, { Component } from 'react';
import { Grid, Row, Progress } from 'rsuite';
import Element from '../elements/Element';
import RowTop from '../elements/RowTop';
import FormatData from './formatData';
import ChartBar from '../charts/Bar';
import ChartPie from '../charts/Pie';
import ChartHorizontalBar from '../charts/HorizontalBar';

const { Circle } = Progress;
 
class P1Madres extends Component{
    state = {
        error: false,
        length: 0,
        cargarLength: false,
        createdOn: {},
        cargarCreatedOn: false,
        grupo: {},
        cargarGrupo: false,

        entidad: {},
        cargarEntidad: false,
        areaUnidad: {},
        cargarAreaUnidad: false,
        beneficiarioINSABI: {},
        cargarBeneficiarioINSABI: false,
        clinica: {},
        cargarClinica: false,

        edad: {},
        cargarEdad: false,
        estaEmbarazada: {},
        cargarEstaEmbarazada: false,
        sexoHijo: {},
        cargarSexoHijo: false,
        edadHijo: {},
        cargarEdadHijo: false,

        parentesco: {},
        cargarParentesco: false,
        numPersonasHogar: {},
        cargarNumPersonasHogar: false,
        posicionHijo: {},
        cargarPosicionHijo: false,
        numMenores: {},
        cargarNumMenores: false,
        estadoCilvil: {},
        cargarEstadoCilvil: false,
        escolaridad: {},
        cargarEscolaridad: false,
        dedicacion: {},
        cargarDedicacion: false,
        programaSocial: {},
        cargarProgramaSocial: false,
    }


    getDataChartClinica = async (data) =>{
        let array = [];
        let dataTemp = [];
        let dataValue = [];

        await data.forEach(e => {
            if (!dataTemp.includes(e)){
                dataTemp.push(e)
            }
        });

        dataValue = dataTemp.map(e => {
            return data.filter(ee => e === ee).length;
        });

        for (let i = 0; i <= dataTemp.length - 1; i++){
            array.push({
                "clave": dataTemp[i],
                "registros": dataValue[i]
            });
        }
        
        this.props.setDataPanel2(array);
    }

    componentDidMount = async() =>{
        let obj = new FormatData();
        let data = await obj.fetchDataAll();

        if (data !== "error")
        {
            this.setState({
                length: data.length,
                cargarlength: true,
                createdOn: await obj.getDataChartCreatedOn(data.createdOn),
                cargarCreatedOn: true,
                grupo: await obj.getDataChartGrupo(data.grupo),
                cargarGrupo: true,
                entidad: await obj.getDataChartEntidad(data.entidad),
                cargarEntidad: true,
                areaUnidad: await obj.getDataChartAreaUnidad(data.areaUnidad),
                cargarAreaUnidad: true,
                beneficiarioINSABI: await obj.getDataChartBeneficiarioINSABI(data.beneficiarioINSABI),
                cargarBeneficiarioINSABI: true,
                edad: await obj.getDataChartEdad(data.edad),
                cargarEdad: true,
                estaEmbarazada: await obj.getDataChartEstaEmbarazada(data.estaEmbarazada),
                cargarEstaEmbarazada: true,
                sexoHijo: await obj.getDataChartSexoHijo(data.sexoHijo),
                cargarSexoHijo: true,
                edadHijo: await obj.getDataChartEdadHijo(data.edadHijo),
                cargarEdadHijo: true,
                parentesco: await obj.getDataChartParentesco(data.parentesco),
                cargarParentesco: true,
                posicionHijo: await obj.getDataChartPosicionHijo(data.posicionHijo),
                cargarPosicionHijo: true,
                numPersonasHogar: await obj.getDataChartNumPersonasHogar(data.numPersonasHogar),
                cargarNumPersonasHogar: true,
                numMenores: await obj.getDataChartNumMenores(data.numMenores),
                cargarNumMenores: true,
                estadoCilvil: await obj.getDataChartEstadoCilvil(data.estadoCilvil),
                cargarEstadoCilvil: true,
                escolaridad: await obj.getDataChartEscolaridad(data.escolaridad),
                cargarEscolaridad: true,
                dedicacion: await obj.getDataChartDedicacion(data.dedicacion),
                cargarDedicacion: true,
                programaSocial: await obj.getDataChartProgramaSocial(data.programaSocial),
                cargarProgramaSocial: true,
            });

            // await this.getDataChartClinica(data.clinica);
            
        } else {
            this.setState({
                error: true
            });
        }
    }

    render(){
        if (this.state.error){
            return(
                <h1>Error</h1>
            );
        }

        return(
            <Grid fluid style={{margin:0, padding:0}}>
                {/* Actividades generales */}
                { this.state.cargarCreatedOn ?
                    <RowTop
                        length={this.state.length}
                        createdOn={this.state.createdOn}
                    />
                    : <Row className="box-element-container">
                        <Element md={8} /><Element md={8} /><Element md={8} />
                    </Row>
                }
                {/* Porcentajes morelos/yucatan */}
                <Row className="box-element-container">
                    { this.state.cargarEntidad ?
                        <Element title="Registros Morelos" md={12} fill={1}>
                            <Circle  style={{width: "30%", margin:10}} percent={parseFloat(this.state.entidad.morelos)} strokeColor="#ffc107" />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarEntidad ?
                        <Element title="Registros Yucatan" md={12} fill={1}>
                            <Circle style={{width: "30%", margin:10}} percent={parseFloat(this.state.entidad.yucatan)} strokeColor="#b3b3ff" />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>

                {/* Graficas de grupo, Area unidad y Beneficiario insabi*/}
                <Row className="box-element-container">
                    { this.state.cargarGrupo ?
                        <Element title="Grupo" md={8} fill={1}>
                            <ChartPie
                                width={150}
                                data={[this.state.grupo.control, this.state.grupo.intervencion]} 
                                labels={["Control", "Intervencin"]}
                            />
                        </Element>
                        :<Element md={8}/>
                    }
                    { this.state.cargarAreaUnidad ?
                        <Element title="rea de unidad" md={8} fill={1}>
                            <ChartPie
                                width={150}
                                labels={["Rural", "Urbana"]} 
                                data={[this.state.areaUnidad.rural, this.state.areaUnidad.urbano]}
                            />
                        </Element>
                        : <Element md={8} />
                    }
                    { this.state.cargarBeneficiarioINSABI ?
                        <Element title="Beneficiari@s INSABI" md={8} fill={1}>
                            <ChartPie
                                width={150}
                                labels={["Si", "No", "No sabe", "Sin resp."]} 
                                data={[this.state.beneficiarioINSABI._1_, this.state.beneficiarioINSABI._2_, this.state.beneficiarioINSABI._99_, this.state.beneficiarioINSABI.nan]}
                            />
                        </Element>
                        : <Element md={8} />
                    }
                </Row>
                
                {/* Graficas de Edad y Esta embarazada */}
                <Row className="box-element-container">
                    { this.state.cargarEdad ?
                        <Element title="Frecuencia de edades (Cuidadora)" md={13} fill={1}>
                            <ChartPie
                                width={200}
                                labels={["11-20 aos", "21-30 aos", "31-40 aos", "41-50 aos", "+50 aos", "Sin resp."]}
                                data={[this.state.edad._1_, this.state.edad._2_, this.state.edad._3_, this.state.edad._4_, this.state.edad._5_, this.state.edad.nan]}
                            />
                        </Element>
                        : <Element md={13} />
                    }
                    { this.state.cargarEstaEmbarazada ?
                        <Element title="Actualmente esta embarazada?" md={11} fill={1}>
                            <ChartBar
                                label="Registros"
                                width={200}
                                data={[this.state.estaEmbarazada._1_, this.state.estaEmbarazada._2_, this.state.estaEmbarazada._8666_, this.state.estaEmbarazada.nan]} 
                                labels={["Si", "No", "No sabe", "Sin resp."]}
                            />
                        </Element>
                        :<Element md={11}/>
                    }
                </Row>

                {/* Graficas de Edad hijo y sexo hijo */}
                <Row className="box-element-container">
                    { this.state.cargarSexoHijo ?
                        <Element title="Sexo hij@" md={11} fill={1}>
                            <ChartBar
                                label="Registros"
                                width={200}
                                data={[this.state.sexoHijo._1_, this.state.sexoHijo._2_, this.state.sexoHijo._3_, this.state.sexoHijo.nan]} 
                                labels={["Hombre", "Mujer", "Embarazada", "Sin resp."]}
                            />
                        </Element>
                        :<Element md={11}/>
                    }
                    { this.state.cargarEdadHijo ?
                        <Element title="Frecuencia de edades (Hij@)" md={13} fill={1}>
                            <ChartPie
                                width={200}
                                labels={["0-10 meses", "11-20 meses", "21-30 meses", "31-40 meses", "+40 meses", "Sin resp."]}
                                data={[this.state.edadHijo._1_, this.state.edadHijo._2_, this.state.edadHijo._3_, this.state.edadHijo._4_, this.state.edadHijo._5_, this.state.edadHijo.nan]}
                            />
                        </Element>
                        : <Element md={13} />
                    }
                </Row>

                {/* Graficas de parentesco/Numero de personas en el hogar */}
                <Row className="box-element-container">
                    { this.state.cargarParentesco ?
                        <Element title="Parentesco ni@" md={12} fill={1}>
                            <ChartHorizontalBar 
                                width={250}
                                label="Registros"
                                labels={["Madre", "Abuela", "Tia/Hermana", "Padre/Abuelo", "Tio/Hermano", "Ningn parentesco", "Otro parentesco", "No sabe", "Sin resp."]} 
                                data={[this.state.parentesco._1_, this.state.parentesco._2_, this.state.parentesco._3_, this.state.parentesco._4_, this.state.parentesco._5_, this.state.parentesco._6_, this.state.parentesco._1111_, this.state.parentesco._8666_ , this.state.parentesco.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarNumPersonasHogar ?
                        <Element title="Nmero de personas en el hogar" md={12} fill={1}>
                            <ChartBar 
                                width={250}
                                label="Registros" 
                                labels={["1-2 Personas", "3-4 Personas", "5-6 Personas", "+7 Personas", "Sin resp."]}
                                data={[this.state.numPersonasHogar._1_, this.state.numPersonasHogar._2_, this.state.numPersonasHogar._3_, this.state.numPersonasHogar._4_, this.state.numPersonasHogar.nan]} 
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>

                {/* Graficas de Posicion hijo/Numero de menores */}
                <Row className="box-element-container">
                    { this.state.cargarPosicionHijo ?
                        <Element title="Posicin ni@" md={12} fill={1}>
                            <ChartBar 
                                width={200}
                                label="Registros"
                                labels={["P. 1", "P. 2", "P. 3", "P. +4", "Sin resp."]} 
                                data={[this.state.posicionHijo._1_, this.state.posicionHijo._2_, this.state.posicionHijo._3_, this.state.posicionHijo._4_, this.state.posicionHijo.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarNumMenores ?
                        <Element title="Menores de 5 aos (hogar)" md={12} fill={1}>
                            <ChartBar 
                                width={200} 
                                label="Registros" 
                                labels={["0 M.", "1 M.", "2 M.", "+3 M.", "Sin resp."]}
                                data={[this.state.numMenores._1_, this.state.numMenores._2_, this.state.numMenores._3_, this.state.numMenores._4_, this.state.numMenores.nan]} 
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>
                 
                 {/* Graficas de Estado civil/Escolaridad */}
                <Row className="box-element-container">
                    { this.state.cargarEstadoCilvil ?
                        <Element title="Estado civil" md={12} fill={1}>
                            <ChartHorizontalBar
                                width={300}
                                label="Registros"
                                labels={["Soltera", "Casada", "Vive en unin libre", "Separada", "Divorciada", "Viuda", "Sin resp."]} 
                                data={[this.state.estadoCilvil._1_, this.state.estadoCilvil._2_, this.state.estadoCilvil._3_, this.state.estadoCilvil._4_, this.state.estadoCilvil._5_, this.state.estadoCilvil._6_, this.state.estadoCilvil.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarEscolaridad ?
                        <Element title="Escolaridad" md={12} fill={1}>
                            <ChartHorizontalBar 
                                width={300}
                                label="Registros" 
                                labels={[
                                    "Preescolar/Kinder", 
                                    "Primaria", 
                                    "Secundaria", 
                                    "Preparatoria/Bachillerato",
                                    "Normal bsica", 
                                    "Carrera tcnica/comercial", 
                                    "Profesional (universidad)", 
                                    "Posgrado", 
                                    "Ninguno", 
                                    "No sabe", 
                                    "Sin resp."
                                ]}
                                data={[
                                    this.state.escolaridad._1_, 
                                    this.state.escolaridad._2_, 
                                    this.state.escolaridad._3_, 
                                    this.state.escolaridad._4_, 
                                    this.state.escolaridad._5_, 
                                    this.state.escolaridad._6_, 
                                    this.state.escolaridad._7_, 
                                    this.state.escolaridad._8_, 
                                    this.state.escolaridad._8555_, 
                                    this.state.escolaridad._8666_, 
                                    this.state.escolaridad.nan
                                ]} 
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>

                 {/* Graficas de Dedicacion/Programa socials */}
                 <Row className="box-element-container">
                    { this.state.cargarDedicacion ?
                        <Element title="Principal actividad" md={12} fill={1}>
                            <ChartHorizontalBar
                                width={200}
                                label="Registros"
                                labels={["Ama de casa", "Trabajadora/Empleada", "Estudiante", "Otro", "Sin resp."]} 
                                data={[this.state.dedicacion._1_, this.state.dedicacion._2_, this.state.dedicacion._3_, this.state.dedicacion._1111_, this.state.dedicacion.nan]}
                            />
                        </Element>
                        : <Element md={12} />
                    }
                    { this.state.cargarProgramaSocial ?
                        <Element title="Pertenencia a programa social" md={12} fill={1}>
                            <ChartPie
                                width={200}
                                labels={[
                                    "Si", "No", "No sabe", "Sin resp."
                                ]}
                                data={[
                                    this.state.programaSocial._1_, this.state.programaSocial._2_, this.state.programaSocial._8666_, this.state.programaSocial.nan
                                ]} 
                            />
                        </Element>
                        : <Element md={12} />
                    }
                </Row>
            </Grid>
        );
    }
}

export default P1Madres;