import React, { Component, Fragment } from 'react';
import { Grid, Row, Loader } from 'rsuite';
import ElementTetx from '../elements/ElementTexts';
import URL_API from  '../../assets/conf';
import Item from './item';

const DICT = {
    1: {
        table: 'madres',
        labels: ["", "", ""],
        LabelsObj: ["", "", ""]
    },
    2: {
        table: 'cuidadoras',
        labels: ["Edad", "Edad bebe/nio (meses)", "Parentesco"],
        LabelsObj: ["Created_On", "edadMama", "edadBebe", "parentescoBebe"],
        parentescoBebe: ["Hij@", "Niet@", "Sobrin@", "Herman@", "Otr@", "Sin responder"]
    },
    3: {
        table: 'proveedores',
        labels: ["", "", ""],
        LabelsObj: ["", "", ""]
    },
    4: {
        table: 'nutres',
        labels: ["", "", ""],
        LabelsObj: ["", "", ""]
    }
}

class Panel1 extends Component{
    state = {
        dataTable: "",
        cargarDataTable: false,
    }

    fetchDataAll = async () => {
        let table = DICT[this.props.search.table].table;

        try {
            let resp = await fetch(`${URL_API}/${table}`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                this.setState({
                    dataTable: data.data,
                    cargarDataTable: true,
                });
            }
        } catch (error) {
            this.setState({
                dataTable: "error",
                cargarDataTable: false,
            });
        }
    }

    async componentDidMount(){
        if (this.props.search.type === "all"){
            await this.fetchDataAll(); 
        }
    }

    render(){
        let { search } = this.props;
        let { dataTable } = this.state;

        return(
            <Grid fluid>

            { this.state.cargarDataTable ?
                dataTable.map(e => <Item title={e.id} date={e[DICT[search.table].LabelsObj[0]]}
                    data={[
                        [DICT[search.table].labels[0], e[DICT[search.table].LabelsObj[1]]],
                        [DICT[search.table].labels[1], e[DICT[search.table].LabelsObj[2]]],
                        search.table === 2 
                        ? [DICT[search.table].labels[2], e[DICT[search.table].LabelsObj[3]]] 
                        : [DICT[search.table].labels[2], e[DICT[search.table].LabelsObj[3]]]
                    ]}
                />)
                : <Fragment>
                    <Row className="box-element-container"><ElementTetx md={24} rows={3}/></Row>
                    <Row className="box-element-container"><ElementTetx md={24} rows={3}/></Row>
                    <Row className="box-element-container"><ElementTetx md={24} rows={3}/></Row>
                    <center>
                        <Loader style={{marginTop: 10}} content="cargando..." vertical speed="fast" size="md"/>
                    </center>
                </Fragment> 
            }

            </Grid>
        );
            
    }
}

export default Panel1;