import GetDate from '../../assets/dates';
import URL_API from  '../../assets/conf';

class FormatData{
    fetchDataAll = async () =>{
        try {
            let resp = await fetch(`${URL_API}/madres`);
            let data = await resp.json();

            if (data.msg === "successful")
            {
                data = data.data;

                return {
                    length: data.length,
                    createdOn: await data.map(e => { let date = new Date(e.fe_o);return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}` }),
                    grupo: await data.map(e => { return parseInt(e.cc_1_4) }),
                    
                    entidad: await data.map(e =>{  return parseInt(e.ig_1_1)  }),
                    areaUnidad: await data.map(e =>{  return parseInt(e.ig_1_4)  }),
                    beneficiarioINSABI: await data.map(e =>{  return parseInt(e.ig_1_5)  }),
                    clinica: await data.map(e =>{  return e.cc_1_1  }),

                    edad: await data.map(e =>{  return parseInt(e.dge_1_1)  }),
                    estaEmbarazada: await data.map(e =>{  return parseInt(e.dge_1_2)  }),
                    sexoHijo: await data.map(e =>{  return parseInt(e.dge_1_4)  }),
                    edadHijo: await data.map(e =>{  return parseInt(e.dge_e_ninx)  }),

                    parentesco: await data.map(e => { return parseInt(e.dge_1_6) }),
                    posicionHijo: await data.map(e => { return parseInt(e.dge_1_7) }),
                    numPersonasHogar: await data.map(e => { return parseInt(e.dge_1_8) }),
                    numMenores: await data.map(e => { return parseInt(e.dge_1_9) }),
                    estadoCilvil: await data.map(e => { return parseInt(e.dge_1_10) }),
                    escolaridad: await data.map(e => { return parseInt(e.dge_1_11) }),
                    dedicacion: await data.map(e => { return parseInt(e.dge_1_12) }),
                    programaSocial: await data.map(e => { return parseInt(e.dge_1_15) }),
                }
            }
        } catch (error) {
            return "error";
        }
    }

    getDataChartCreatedOn = async (data) =>{
        let obj = new GetDate();
        let labels = obj.getDates();
        let values = obj.getValuesDates(labels, data);

        return values
    }

    getDataChartGrupo = async (data) =>{
        return {
            control: await data.filter(e => e === 1).length,
            intervencion: await data.filter(e => e === 2).length,
        }
    }

    getDataChartEntidad = async (data) =>{
        let porcentaje = 100 / data.length;

        return {
            morelos: ((data.filter(e => e === 17).length) * porcentaje).toFixed(2),
            yucatan: ((data.filter(e => e === 31).length) * porcentaje).toFixed(2),
        }
    }

    getDataChartAreaUnidad = async (data) =>{
        return {
            urbano: await data.filter(e => e === 2).length,
            rural: await data.filter(e => e === 1).length,
        }
    }

    getDataChartBeneficiarioINSABI = async (data) =>{
        return {
            _1_ : await data.filter(e => e === 1).length,
            _2_ : await data.filter(e => e === 2).length,
            _99_ : await data.filter(e => e === 99).length,
            nan : await data.filter(e => e !== 1 && e !== 2 && e !== 99).length,
        }
    }

    getDataChartEdad = async (data) =>{
        return{
            _1_: await data.filter(e => e >= 11 && e <= 20).length,
            _2_: await data.filter(e => e >= 21 && e <= 30).length,
            _3_: await data.filter(e => e >= 31 && e <= 40).length,
            _4_: await data.filter(e => e >= 41 && e <= 50).length,
            _5_: await data.filter(e => e >= 51).length,
            nan: await data.filter(e => isNaN(e)).length,
        }
    }

    getDataChartEstaEmbarazada = async (data) =>{
        return {
            _1_: await data.filter(e => e === 1).length,
            _2_: await data.filter(e => e === 2).length,
            _8666_: await data.filter(e => e === 8666).length,
            nan: await data.filter(e => isNaN(e)).length,
        }
    }

    getDataChartSexoHijo = async (data) =>{
        return {
            _1_: await data.filter(e => e === 1).length,
            _2_: await data.filter(e => e === 2).length,
            _3_: await data.filter(e => e === 9).length,
            nan: await data.filter(e => isNaN(e)).length,
        }
    }

    getDataChartEdadHijo = async (data) =>{
        return {
            _1_: await data.filter(e => e >= 0 && e <= 10).length,
            _2_: await data.filter(e => e >= 11 && e <= 20).length,
            _3_: await data.filter(e => e >= 21 && e <= 30).length,
            _4_: await data.filter(e => e >= 31 && e <= 40).length,
            _5_: await data.filter(e => e >= 41).length,
            nan: await data.filter(e => isNaN(e)).length,
        }
    }

    getDataChartParentesco = async (data) =>{
        return {
            _1_: await data.filter(e => e === 1).length,
            _2_: await data.filter(e => e === 2).length,
            _3_: await data.filter(e => e === 3).length,
            _4_: await data.filter(e => e === 4).length,
            _5_: await data.filter(e => e === 5).length,
            _6_: await data.filter(e => e === 6).length,
            _1111_: await data.filter(e => e === 1111).length,
            _8666_: await data.filter(e => e === 8666).length,
            nan: await data.filter(e => e !==1 && e !==2 && e !==3 && e !==4 && e !==5 && e !==6 && e !==1111 && e !==8666).length,
        }
    }

    getDataChartPosicionHijo = async (data) =>{
        return {
            _1_: await data.filter(e => e === 1 || e === 0).length,
            _2_: await data.filter(e => e === 2).length,
            _3_: await data.filter(e => e === 3).length,
            _4_: await data.filter(e => e >= 4).length,
            nan: await data.filter(e => isNaN(e)).length,
        }
    }
    
    getDataChartNumPersonasHogar = async (data) =>{
        return {
            _1_: await data.filter(e => e === 1 || e === 2).length,
            _2_: await data.filter(e => e === 3 || e === 4).length,
            _3_: await data.filter(e => e === 5 || e === 6).length,
            _4_: await data.filter(e => e >= 7).length,
            nan: await data.filter(e => isNaN(e)).length,
        }
    }

    getDataChartNumMenores = async (data) =>{
        return {
            _1_: await data.filter(e => e === 0).length,
            _2_: await data.filter(e => e === 1).length,
            _3_: await data.filter(e => e === 2).length,
            _4_: await data.filter(e => e >= 3).length,
            nan: await data.filter(e => isNaN(e)).length,
        }
    }
    
    getDataChartEstadoCilvil = async (data) =>{
        return {
            _1_: await data.filter(e => e === 1).length,
            _2_: await data.filter(e => e === 2).length,
            _3_: await data.filter(e => e === 3).length,
            _4_: await data.filter(e => e === 4).length,
            _5_: await data.filter(e => e === 5).length,
            _6_: await data.filter(e => e === 6).length,
            nan: await data.filter(e => e !==1 && e !==2 && e !==3 && e !==4 && e !==5 && e !==6).length,
        }
    }
    
    getDataChartEscolaridad = async (data) =>{
        return {
            _1_: await data.filter(e => e === 1).length,
            _2_: await data.filter(e => e === 2).length,
            _3_: await data.filter(e => e === 3).length,
            _4_: await data.filter(e => e === 4).length,
            _5_: await data.filter(e => e === 5).length,
            _6_: await data.filter(e => e === 6).length,
            _7_: await data.filter(e => e === 7).length,
            _8_: await data.filter(e => e === 8).length,
            _8555_: await data.filter(e => e === 8555).length,
            _8666_: await data.filter(e => e === 8666).length,
            nan: await data.filter(e => e !==1 && e !==2 && e !==3 && e !==4 && e !==5 && e !==6 && e !==7  && e !==8  && e !==8555 && e !==8666).length,
        }
    }

    getDataChartDedicacion = async (data) =>{
        return{
            _1_: await data.filter(e => e === 1).length,
            _2_: await data.filter(e => e === 2).length,
            _3_: await data.filter(e => e === 3).length,
            _1111_: await data.filter(e => e === 1111).length,
            nan: await data.filter(e => e !==1 && e !==2 && e !==3 && e !==1111).length,
        }
    }
    
    getDataChartProgramaSocial = async (data) =>{
        return {
            _1_: await data.filter(e => e === 1).length,
            _2_: await data.filter(e => e === 2).length,
            _8666_: await data.filter(e => e === 8666).length,
            nan: await data.filter(e => e !==1 && e !==2 && e !==8666).length,
        }
    }
}

export default FormatData;