import React, { Component, Fragment } from 'react';
import Header from '../components/Header';
import MainPanel from '../components/mainPanel';

class MainContainer extends Component{
    state = {
        option: this.props.navOption,
        search: {
            table: "",
            type: "",
            id: ""
        }
    }

    handleUpdateNavOption = (option, tableSearch, type, idSearch = null) =>{
        if( idSearch !== null)
        {
            this.setState({
                option: option,
                search: {
                    table: tableSearch,
                    type: type,
                    id: idSearch
                }
            });
        }
        else
        {
            this.setState({
                option: option,
                search: {
                    table: tableSearch,
                    type: type
                }
            });
        }
    }

    render(){
        const {title} = this.props;
        
        return(
            <Fragment>
                <header className="header">
                    <Header title={title} updateOption={this.handleUpdateNavOption}/>
                </header>
                <section className="main_panel">
                    <MainPanel option={this.state.option} search={this.state.search}/>
                </section>
            </Fragment>
        );
    }
}

export default MainContainer;