import React, { Component, Fragment } from 'react';
import Panel1 from './panel1';

import { Loader, Table } from 'rsuite';
const { Column, HeaderCell, Cell } = Table;

// Divide y vencesar-> crear grupos pequeos para soluxionar el problema
// Gratis porque somosel produto
// La manipulacion comienzacon un soloindividuo

class Madres extends Component{
    state={
        data: [],
        cargarTable: false,
    }

    setDataPanel2 = (_data_) =>{
        this.setState({
            data: _data_,
            cargarTable: true,
        });
    }

    render(){
        return(
            <Fragment>
                <section className="main_panel1">
                    <section className="panel_contect">
                        <Panel1 setDataPanel2={this.setDataPanel2} />
                    </section>
                </section>
                <section className="main_panel2">
                    <section className="panel_contect">
                        <div style={{background: "transparent"}} className="box-element-content box-element-content-response">
                            { this.state.cargarTable ? 
                                    <Table
                                    height={500}
                                    data={this.state.data}
                                    >
                                        <Column width={150} fixed>
                                        <HeaderCell>Clave</HeaderCell>
                                        <Cell dataKey="clave" />
                                        </Column>
                                        <Column width={100}>
                                        <HeaderCell>Registros</HeaderCell>
                                        <Cell dataKey="registros" />
                                        </Column>
                                    </Table>
                                :   <center>
                                        <Loader style={{marginTop: 10}} content="cargando..." vertical speed="fast" size="md"/>
                                    </center>
                            }
                        </div>
                    </section>
                </section>
            </Fragment>
        );
    }
}

export default Madres;