import React from 'react';
import { Row, Button } from 'rsuite';
import Element from '../elements/Element';
import Icon from '../../assets/icons/circle.png';
import '../../styles/search.css';

function Item(props){
    return (
        <Row className="box-element-container">
            <Element fill={1} md={24}>
                <div className="box-element-search">
                    <div className="box-element-search-img">
                        <img src={Icon} alt="Icono Item"/>
                    </div>
                    <div className="box-element-search-content">
                        <div className="search-content_header">
                            <span>{props.title}</span>
                            <span>{props.date}</span>
                        </div>
                        {
                            props.data.map(e => <p><span>{e[0]}: </span>{e[1]}</p>)
                        }
                    </div>
                    <div className="box-element-search-button">
                        <Button>Ver</Button>
                    </div>
                </div>
            </Element>
        </Row>
    );
}

export default Item;