import React, { Component, Fragment } from 'react';
import Panel1 from './Panel1';
import { Row } from 'rsuite';
import URL_API from '../../assets/conf';
import Element from '../elements/Element';
import ChartLine from '../charts/LineCreatedOn';

class Home extends Component{
    state ={
        cargar: false,
        dates: {},
        dates2: {},
        error: false
    }

    fetchDates = async () =>{
        try {
            let resp = await fetch(`${URL_API}/home/dates/1/2020-08-01`);
            let resp2 = await fetch(`${URL_API}/home/dates/2/2020-08-01`);
            let data = await resp.json();
            let data2 = await resp2.json();

            if (data.msg === "successful" && data2.msg === "successful")
            {
                return {
                    1: data.data,
                    2: data2.data
                };
            }
        } catch (error) {
            return "error";
        }
    }

    handleGetData = async () =>{
        let data = await this.fetchDates();

        if (data[1] !== 'error' && data[2] !== 'error')
        {
            this.setState({
                dates: {
                    name: data[1].name,
                    value: data[1].value
                },
                dates2: {
                    name: data[2].name,
                    value: data[2].value
                },
                cargar: true
            });
        }else{
            this.setState({
                error: true
            });
        }
    }

    componentDidMount = async () =>{
        await this.handleGetData();
    }

    render(){
        return(
            <Fragment>
                <section className="main_panel1">
                    <section className="panel_contect">
                        <Panel1 />
                    </section>
                </section>
                <section className="main_panel2">
                    <section className="panel_contect">
                        <Row className="box-element-container">
                            <Element title="Actividad Nutres" md={24} fill={1}>
                                <ChartLine data1={this.state.dates}/>
                            </Element>
                        </Row>
                        <Row className="box-element-container">
                            <Element title="Actividad Madres" md={24} fill={1}>
                                <ChartLine data1={this.state.dates2}/>
                            </Element>
                        </Row>
                    </section>
                </section>
            </Fragment>
        );
    }
}

export default Home;