import React, { Component } from 'react';
import { Input, InputGroup, Icon, SelectPicker, Alert } from 'rsuite';
// import URL_API from '../assets/conf';
import SELECT_OPTIONS from '../assets/selectOpctions';
import '../styles/header.css';

import { Modal, Button, ButtonToolbar , Loader, Placeholder} from 'rsuite';
const {Paragraph} = Placeholder;

class Header extends Component{
    constructor(props) {
        super(props);
        this.state = {
            tableSearch: "",
            idSearch: "",
            validId: false,

            show: false,
            data: [],
            cargarData: true
        };
        this.close = this.close.bind(this);
        this.open = this.open.bind(this);
    }

    close() {
        this.setState({ show: false });
    }

    open(event) {
        this.setState({ show: true });
    }
    
    handleUpdateTableSearch = (e) => {
        this.setState({
            tableSearch: e
        });
    }

    handleUpdateIdSearch = (e) => {
        this.setState({
            idSearch: e
        });
    }

    handleChechId = async () =>{
        let {tableSearch, idSearch} = this.state;

        if(tableSearch !== "")
        {
            if(idSearch !== "")
            {
                try {
                    // let resp = await fetch(`${URL_API}/checkid/${tableSearch}+${idSearch}`);
                    // let data = await resp.json();

                    // data = data.msg === "successful" ? data.value : "error";
                    // data === 1 ? this.props.updateOption(0, tableSearch, "id", idSearch)
                    //             : Alert.warning('Id no encontrado')

                    this.setState({
                        validId: true,
                        show: true
                    });
                    
                } catch (error) {
                    Alert.error('Ha ocurrido un error')
                }
            } else this.props.updateOption(0, tableSearch, "all");
        } else Alert.warning('Seleccione una opcin')
    }

    render(){
        const {title} = this.props;
        const { show } = this.state;

        return (
            <div className="header-container">
                <div className="header-title">
                    <span>Dashboard | {title}</span>
                </div>

                <div className="header-search">
                    <SelectPicker
                        searchable={false}
                        size="md"
                        data={SELECT_OPTIONS}
                        appearance="subtle"
                        placeholder="Seleccione una opcin"
                        onSelect={this.handleUpdateTableSearch}
                        style={{
                            width: "35%", 
                            "background":"#bdd3ca",
                            borderRadius: 5
                        }}
                    />
                    <InputGroup 
                        size="md" inside 
                        style={{ width: "55%"}}
                        >
                        <Input placeholder="Id" onPressEnter={this.handleChechId} onChange={this.handleUpdateIdSearch}/>
                        <InputGroup.Button onClick={this.handleChechId}>
                            <Icon icon="search" />
                        </InputGroup.Button>
                    </InputGroup>
                </div>

                <div className="header-other">
                    <div className="modal-container">
                        {/* <ButtonToolbar>
                                <Button onClick={this.open}>Open</Button>
                        </ButtonToolbar> */}
                
                        {/* <Modal show={show} onHide={this.close} onExited={this.resetRows}> */}
                        <Modal show={show} onHide={this.close}>
                            <Modal.Header>
                                <Modal.Title>Modal Title</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                {this.state.cargarData ? (
                                    <Paragraph rows={10} />
                                ) : (
                                    <div style={{ textAlign: 'center' }}>
                                    <Loader size="md" />
                                    </div>
                                )}
                            </Modal.Body>
                            <Modal.Footer>
                                <Button onClick={this.close} appearance="primary">
                                    Ok
                                </Button>
                            </Modal.Footer>
                        </Modal>
                    </div>
                </div>
            </div>
        );
    }
}

export default Header;