class GetDates{
    getDates = () => {
        const dateStart = new Date('2020-08-01');
        const day = new Date();
        let dates = [];
    
        while(day.getTime() >= dateStart.getTime()){
            dateStart.setDate(dateStart.getDate() + 1);
            dates.push(`${dateStart.getDate()}/${(dateStart.getMonth() + 1)}/${dateStart.getFullYear()}`);
        }
    
        return dates;
    }
    
    getValuesDates = async (dates, _values_) =>{
        let values = [];
        
        values = dates.map(date => {
            let temp = _values_.filter(e => e === date).length;
            return temp
        });
    
        return {
            name: dates,
            value: values
        }
    }
}

export default GetDates;